import { isJSExpression } from '@alilc/lowcode-types'; // 1.loop 
// 2.loop  0

export default function isUseLoop(loop, isDesignMode) {
  if (isJSExpression(loop)) {
    return true;
  }

  if (!Array.isArray(loop)) {
    return false;
  }

  if (!isDesignMode) {
    return true;
  }

  return loop.length > 0;
}